@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@200&display=swap");
.lists {
  list-style: none;
  font-weight: 500;
  font-size: 10;
  text-align: center;
  text-align: justify;
  border: 1px solid #0084ff;
  border-radius: 20px;
  width: auto;
  padding: 0 15px;
  color: #0084ff;
  height: 30px;
  text-decoration: none;
  margin: 10px 3px;
  background-color: transparent;
  transition: 0.5s;
}

.lists:hover {
  transform: scale(1.04);

  color: white;
  background-image: -webkit-linear-gradient(181deg, #0048ff, #0096ff),
    -webkit-linear-gradient(#000, #000);
  background-image: -o-linear-gradient(181deg, #0048ff, #0096ff),
    -o-linear-gradient(#000, #000);
  background-image: linear-gradient(269deg, #0048ff, #0096ff),
    linear-gradient(#000, #000);
}
.captions {
  width: fit-content;
  margin: 0;
  /* font-family: "Raleway", sans-serif; */
  font-size: 24px;
  font-weight: 600;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.25;
  letter-spacing: -0.6px;
  text-align: left;
  color: #000;
}
.list_icon {
  margin-top: 30px;
  margin-bottom: 15px;
  margin-left: 5px;
}
.list_icon > i {
  margin-right: 10px;
  color: #0048ff;
}
.list_icon span {
  margin-block-start: 1em;
  margin-block-end: 1em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  font-weight: 800;
  font-family: "Raleway", sans-serif;
}
