.trendingSection {
  margin-top: 59px;
}
h1 {
  margin: 0;
  /* font-family: Raleway, sans-serif; */
  font-size: 2.25em;
  font-weight: 500;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.94;
  letter-spacing: -0.9px;
  text-align: left;
  color: #000;
}
.trendinContainer {
  width: 100%;
  display: flex;
  flex-direction: row;
  margin-bottom: 28px;
}
.trending_left {
  width: 90%;
  margin-right: 10px;
}
.trending_left img {
  border-radius: 7px;
  border: 1px solid lightseagreen;
  cursor: pointer;
}

.trending_right {
  display: flex;
  flex-direction: column;
}
.trending_right img {
  margin-bottom: 10px;
  display: flex;
  flex-direction: column;
  width: 76%;
  border: 1px solid lightseagreen;
  border-radius: 7px;
  cursor: pointer;
}
