@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@200&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,200;1,100&display=swap");
header {
  display: flex;
  justify-content: space-between;
  height: 80px;
  text-align: center;
  align-items: center;
  background-color: white;
  box-shadow: 1px 1px 3px lightgray;
  width: 100%;
  position: fixed;
}
.left_menu {
  display: flex;
  justify-content: center;
}

.logo_item {
  text-transform: uppercase;
  font-size: 30px;
  margin: 0px 60px;
  font-weight: 1000;
  font-family: "Palanquin Dark", sans-serif;
  cursor: pointer;
  background-image: -webkit-linear-gradient(181deg, #0048ff, #0096ff),
    -webkit-linear-gradient(#000, #000);
  background-image: -o-linear-gradient(181deg, #0048ff, #0096ff),
    -o-linear-gradient(#000, #000);
  background-image: linear-gradient(269deg, #0048ff, #0096ff),
    linear-gradient(#000, #000);
  color: #0084ff;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}
.logo_un {
  font-size: 18px;
  margin-top: -10px;
  font-weight: 400;
  font-style: italic;
  letter-spacing: 1px;
  margin-left: -78px;
  cursor: pointer;
}
.dropdown {
  margin: 20px 7px;
  cursor: pointer;
}

.explor {
  width: 20px;
  border-style: none;
  cursor: pointer;
  scroll-behavior: smooth;
  align-items: center;
}
.right_menu {
  display: flex;
  margin-top: auto;
  margin-bottom: auto;
}
.menu_text {
  display: flex;
  justify-content: center;
  margin: 20px 0px;
  min-width: 23em;
  opacity: 0.6;
  font-family: "Raleway", sans-serif;
  font-size: 15px;
  font-weight: 800;
  font-style: normal;
  font-stretch: normal;

  letter-spacing: 0.9px;
  text-align: left;
  color: rgba(0, 0, 0, 0.7);
}
.text_under {
  margin-top: -2px;
  letter-spacing: 0.9px;
  text-align: left;
  font-weight: 800;
  color: rgba(0, 0, 0, 0.7);
}
.btn {
  background: #0084ff;
  background: -webkit-linear-gradient(269deg, #0048ff, #0096ff);
  background: -webkit-linear-gradient(181deg, #0048ff, #0096ff);
  background: -o-linear-gradient(181deg, #0048ff, #0096ff);
  background: linear-gradient(269deg, #0048ff, #0096ff);
  border: none;
  font-size: 0.65em;
  border-radius: 30px;
  margin: 20px 60px;
  letter-spacing: 0.5px;
  padding: 0.2em 2em;
  cursor: pointer;
  color: white;
  text-transform: uppercase;
  width: auto;
  font-family: Raleway, sans-serif;
  height: 40px;
  font-weight: 900;
  margin-top: 20px;
  text-align: center;
}
@media (max-width: 768px) {
  .header {
    width: 120%;
  }
}
