.section {
  display: flex;
  max-width: 100%;
  flex-wrap: wrap;
}

.card {
  width: 30%;
  min-height: 25em;
  border: 1px solid skyblue;
  border-radius: 5px;
  box-shadow: 3px 3px 8px #ccc;
  margin: 10px 10px;
  overflow: hidden;
  background-color: white;
  cursor: pointer;
  transition: 0.5s;
}

.card:hover {
  transform: scale(1.04);
}

.card > img {
  height: 180px;
  width: 100%;
}

.card > h2 {
  display: block;
  margin-block-start: 1em;
  margin-block-end: 1em;
  margin-inline-start: 0px;
  line-clamp: 2;

  margin-inline-end: 0px;
  margin: 0 20px;
  font-weight: 600;
  font-size: 1.25em;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  overflow: hidden;
  -webkit-box-orient: vertical;
  white-space: normal;
}
.card_info {
  margin: 0px 20px;
  font-size: 15px;
  margin-top: 8px;
}
.card_info :first-child {
  color: #0084ff;
}

.card_info + p {
  display: block;
  margin-block-start: 1em;
  margin-block-end: 1em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  margin: 10px 20px;
  color: gray;
  white-space: pre-wrap;
  margin-bottom: 15px;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  overflow: hidden;
  -webkit-box-orient: vertical;
  white-space: normal;
}
